.gallery-wrapper {
    background-color: #676464;
    box-sizing: border-box;
    height: 300px;
    overflow: hidden;
    
  }
  
  .gallery-scroll {
    box-sizing: border-box;
    height: 290px;
    overflow-x: auto;
    overflow-y: hidden;
  }
  
  .gallery {
    background-color: #4c4b4b;
    display: flex;
    box-sizing: border-box;
    width: max-content;
    height: 240px;
    padding: 7px 14px ;
  }
  
  .items {
    box-sizing: border-box;
    width: 226px;
    height: 226px;
    /* padding: 17px 14px; */
    
  }
  
  .items img {
    display: flex;
    border: 2px solid #242424;
    width: 226px;
    height: 226px;
  }

